import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
import PropTypes from 'prop-types'

import CircularProgress from '@material-ui/core/CircularProgress'

const styles = theme => ({
  root: {
    display: 'inline-block',
    position: 'relative',
    width: '100%',
    height: '100%'
  },
  cover: {
    height: '100%',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center'
  },
  loader: {
    position: 'absolute'
  },
  imgMask: {
    display: 'inline-block',
    position: 'absolute',
    left: 0,
    bottom: 0,
    width: '100%',
    height: '2rem',
    background: 'linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0,0,0,1));',
    color: '#fff',
    padding: '0 .5rem',
    letterSpacing: '.3rem'
  }
})

class AppImageLoader extends Component {
  constructor () {
    super()
    this.state = {
      image: null,
      isLoadSuccess: true,
      isImageLoaded: false
    }
  }

  componentWillUnmount () {
    this.isCancelled = true
  }

  componentDidMount () {
    const {src} = this.props
    if (src) {
      this.getPreloadImg(3)
        .then(image => {
          setTimeout(() => !this.isCancelled && this.setState({
            image,
            isLoadSuccess: true,
            isImageLoaded: true
          }), 200)
        })
        .catch(error => {
          !this.isCancelled && this.setState({
            isLoadSuccess: false,
            isImageLoaded: true
          })
          console.debug(error)
        })
    }
  }

  getPreloadImg = (retryTimes) => {
    const {src} = this.props
    const image = new Image()

    image.src = src
    return new Promise((resolve, reject) => {
      image.onload = () => resolve(image)
      image.onerror = error => {
        if (retryTimes === 0) {
          reject(error)
        } else {
          setTimeout(() => this.getPreloadImg(retryTimes - 1), 1500)
        }
      }
    })
  }

  render () {
    const {isImageLoaded, isLoadSuccess} = this.state
    const { classes, src, alt, title, loaderSize, backgroundSize } = this.props

    return (
      <div className={classes.root}>
        {
          isImageLoaded && !this.isCancelled
            ? (
              isLoadSuccess
                ? (
                  <div
                    className={classes.cover}
                    alt={alt}
                    style={{
                      backgroundImage: `url(${src})`,
                      backgroundSize: backgroundSize
                    }}>
                    {
                      title &&
                      <span className={classes.imgMask}>
                        {title}
                      </span>
                    }
                  </div>
                )
                : (
                  <div
                    className={classes.cover}
                    alt={alt}
                    style={{
                      backgroundImage: 'url(https://sky172839465.github.io/the-f2e/img/ch06.png)',
                      backgroundSize: backgroundSize
                    }} />
                )
            )
            : (
              <div
                className={classes.loader}
                style={{
                  top: `calc(50% - ${loaderSize / 2}px)`,
                  left: `calc(50% - ${loaderSize / 2}px)`
                }}>
                <CircularProgress size={loaderSize} />
              </div>
            )
        }
      </div>
    )
  }
}

AppImageLoader.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles, { withTheme: true })(AppImageLoader)



