import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
import format from 'date-fns/format'

import Grid from '@material-ui/core/Grid'
import CircularProgress from '@material-ui/core/CircularProgress'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Typography from '@material-ui/core/Typography'

import AppImageLoader from '../Common/AppImageLoader/AppImageLoader'

const styles = theme => ({
  root: {
    margin: '.5rem',
    [theme.breakpoints.up('sm')]: {
      margin: '.5rem 0'
    }
  },
  loader: {
    margin: '5rem',
    textAlign: 'center'
  },
  avator: {
    position: 'relative',
    height: '50vh',
    maxWidth: '100vw',
    overflow: 'hidden',
    width: '100%'
  }
})

class UserContent extends Component {
  constructor () {
    super()
    this.state = {}
  }

  render () {
    const {
      classes,
      isDataLoaded,
      registerUser
    } = this.props
    const {
      address,
      area,
      avator,
      city,
      email,
      name,
      phone,
      timestamp
    } = registerUser
    const [zipCode, areaName] = area ? area.split('|') : []
    const fullAddress = `(${zipCode}) ${city}${areaName}${address}`

    return (
      <div>
        <Grid container justify='center'>
          <Grid item xs={12}>
            {
              isDataLoaded
                ? (
                  registerUser && (Object.keys(registerUser).length > 0)
                    ? (
                      <Card className={classes.root}>
                        <div className={classes.avator}>
                          <AppImageLoader
                            src={avator}
                            alt='avator'
                            backgroundSize='cover'
                            loaderSize={60}
                            title={name} />
                        </div>
                        <CardContent>
                          <Typography gutterBottom variant='caption'>
                            Registered
                          </Typography>
                          <Typography gutterBottom variant='body2'>
                            {format(new Date(timestamp), 'YYYY/MM/DD HH:mm:ss')}
                          </Typography>
                          <Typography gutterBottom variant='caption'>
                            Email
                          </Typography>
                          <Typography gutterBottom variant='body2' component='a' href={`mailTo:${email}`}>
                            {email}
                          </Typography>
                          <Typography gutterBottom variant='caption'>
                            Phone
                          </Typography>
                          <Typography gutterBottom variant='body2'>
                            {phone}
                          </Typography>
                          <Typography gutterBottom variant='caption'>
                            Address
                          </Typography>
                          <Typography gutterBottom variant='body2'>
                            {fullAddress}
                          </Typography>
                        </CardContent>
                      </Card>
                    )
                    : (
                      <Typography
                        variant='title'
                        color='error'
                        align='center'
                        style={{margin: '5rem'}}>
                        Register user not found
                      </Typography>
                    )
                )
                : (
                  <div className={classes.loader}>
                    <CircularProgress size={80} />
                  </div>
                )
            }
          </Grid>
        </Grid>
      </div>
    )
  }
}

export default withStyles(styles)(UserContent)



