import React, { Component } from 'react'
import { connect } from 'react-redux'
import {withRouter} from 'react-router-dom'
import * as actionCreators from '../store/actions'

import UserContent from '../component/User/UserContent'

class User extends Component {
  componentDidMount () {
    const {setCurrentRoute, setDataLoaded, getRegisterUser, match} = this.props
    const {params} = match || {}
    const {key} = params

    setCurrentRoute('/user')
    setDataLoaded(false)
    getRegisterUser(key)
  }

  render () {
    return (
      <main>
        <UserContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    registerUser: state.action.registerUser,
    isDataLoaded: state.action.isDataLoaded
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getRegisterUser: (key) => dispatch(actionCreators.getRegisterUser(key)),
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded)),
    setCurrentRoute: (currentRoute) => dispatch(actionCreators.setCurrentRoute(currentRoute))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(User))



